@import"https://fonts.googleapis.com/css2?family=Montserrat:wght@300;400;500;600;700&display=swap";@import"https://fonts.googleapis.com/css2?family=Inter:wght@400;500;600;700&display=swap";@import"https://fonts.googleapis.com/css2?family=Noto+Sans+SC:wght@400;500;600;700&display=swap";@tailwind base;@tailwind components;@tailwind utilities;:root{--st-bg: #0d0f1a;--st-neon: #00f7ff;--st-pink: #ff00a6;--st-border: rgba(255, 255, 255, 0.08);--anim-speed: 1s;--anim-intensity: 0.7}body{margin:0;padding:0;font-family:"Montserrat","Inter","Noto Sans SC",sans-serif;background-color:rgba(0,0,0,0);color:#e8f9ff;overflow-x:hidden}::-webkit-scrollbar{width:6px;height:6px}::-webkit-scrollbar-track{background:rgba(0,0,0,0)}::-webkit-scrollbar-thumb{background:rgba(0,247,255,.2);border-radius:3px}::-webkit-scrollbar-thumb:hover{background:rgba(0,247,255,.4)}.theme-neon-night{background:radial-gradient(1000px 400px at 10% 10%, rgba(0, 247, 255, 0.08), transparent),radial-gradient(800px 300px at 90% 20%, rgba(255, 0, 166, 0.08), transparent),var(--st-bg);border:1px solid var(--st-border);border-radius:14px;box-shadow:0 0 24px rgba(0,247,255,.15),inset 0 0 24px rgba(255,0,166,.08);position:relative;overflow:hidden;animation:neonPulse calc(var(--anim-speed)*3) ease-in-out infinite}.theme-neon-night::after{content:"";position:absolute;inset:0;pointer-events:none;background:linear-gradient(90deg, transparent, rgba(255, 255, 255, 0.08), transparent);opacity:calc(.16*var(--anim-intensity));animation:shimmerMove calc(var(--anim-speed)*2) ease-in-out infinite}@keyframes neonPulse{0%,100%{box-shadow:0 0 24px rgba(0,247,255,.15),inset 0 0 24px rgba(255,0,166,.08)}50%{box-shadow:0 0 36px rgba(0,247,255,.25),inset 0 0 36px rgba(255,0,166,.14)}}@keyframes shimmerMove{0%{transform:translateX(-100%)}100%{transform:translateX(100%)}}.fade-enter-active,.fade-leave-active{transition:opacity .3s ease}.fade-enter-from,.fade-leave-to{opacity:0}